import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  Button,
  Container,
  Form,
  FormFeedback,
  FormGroup,
  Input,
  Modal,
  ModalBody,
  ModalHeader,
  Spinner,
} from "reactstrap";
import Base from "../Base";
import {
  clearSuccess,
  //sendOtpReducer,
  updatePasswordReducer,
  //verifyOtpReducer,
} from "../reducers/authReducer";
import { useDispatch, useSelector } from "react-redux";
import { ErrorAlert, SuccessAlert } from "../shared-components/AlertBox";

const ChangePassword = () => {
  const args = {
    backdrop: false,
    centered: true,
    size: "sm",
    fade: false,
  };

  const [errors, setErrors] = useState("");
  //const [passwordValid, setPasswordValid] = useState(false);

  const { error, loading, success, registeredUser, userdetails } = useSelector(
    (state) => state.user
  );

  const [formData, setFormData] = useState({
    email: registeredUser
      ? registeredUser
      : userdetails.email
      ? userdetails.email
      : "",
    password: "",
    c_password: "",
  });
  const dispatch = useDispatch();

  const inputHandler = (e) => {
    setFormData({ ...formData, [e.target.id]: e.target.value });
  };

  const validateHandler = (e) => {
    if (formData.password !== formData.c_password) {
      setErrors("Password and Confirm Password not equal");
      //setPasswordValid(false);
    } else {
      setErrors("");
      //setPasswordValid(true);
    }
  };

  const navigate = useNavigate();
  const token = userdetails ? userdetails.token : null;
  useEffect(() => {
    if (token) {
      dispatch(clearSuccess());
      navigate("/");
    }
    if (formData.email === "") {
      dispatch(clearSuccess());
      navigate("/login");
    }
  }, [registeredUser, token]);

  const submitUpdateHandler = async () => {
    await dispatch(updatePasswordReducer(formData));
  };

  return (
    <>
      <Base isFooter="hideFooter">
        <Container>
          <Modal isOpen={true} style={{ pointerEvents: "none" }} {...args}>
            <ModalHeader>Update Password</ModalHeader>
            <ModalBody>
              <Form>
                <FormGroup>
                  <Input
                    type="password"
                    id="password"
                    placeholder="Enter New Password"
                    value={formData.password}
                    onChange={(e) => inputHandler(e)}
                  />
                </FormGroup>

                <FormGroup>
                  <Input
                    type="password"
                    id="c_password"
                    placeholder="Re-enter Password"
                    onChange={(e) => inputHandler(e)}
                    onBlur={(e) => validateHandler(e)}
                  />
                  <FormFeedback>{errors.mobile}</FormFeedback>
                </FormGroup>
              </Form>
              <div className="d-grid py-2">
                <Button
                  className="primary-button"
                  disabled={
                    formData.email === "" ||
                    formData.password === "" ||
                    formData.c_password !== formData.password
                  }
                  onClick={submitUpdateHandler}
                >
                  Update Password
                </Button>
              </div>
            </ModalBody>
            {loading && (
              <span className="position-absolute top-50 start-50 translate-middle">
                <Spinner size="sm" color="primary">
                  Loading...
                </Spinner>
              </span>
            )}
          </Modal>
        </Container>

        {error && <ErrorAlert message={error} />}
        {success && <SuccessAlert message={success} />}
      </Base>
    </>
  );
};

export default ChangePassword;
